.UserHeader {
    display: flex;
    align-items: center;
    justify-content: flex-end; /* Align content from the right side */
    padding: 10px;
    background-color: fffff;
  }
  
  .UserHeader img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px; /* Add some spacing between the image and text */
  }
  
  .UserHeader h1,
  .UserHeader p {
    margin: 0;
  }
  